(document.querySelectorAll("a.image.fit") as NodeListOf<HTMLAnchorElement>).forEach((element: HTMLAnchorElement) =>
{
    element.addEventListener("click", () =>
    {
        const urlParams = new URLSearchParams(element.href)
        const engine = urlParams.get("engine")
        if(!engine)
            return
        window.location.assign(`${engine}.html`)
    }   )
})
